import React from 'react';
import './scss/App.css';
import Todos from './components/Todos';
import Header from './components/Header';
import AddTodo from './components/AddTodo';
import About from './components/pages/About';
import NotFound from './components/pages/NotFound';
import './scss/normalize.css'; 
import {v4 as uuid} from "uuid"; 
import disableScroll from 'disable-scroll';
import {BrowserRouter as Router,Route,Switch} from "react-router-dom"
import axios from "axios";


class App extends React.Component {
constructor(props) {
  super(props);
  this.handleSubmit = this.handleSubmit.bind(this);

  this.state = {
    todos:[], 
    filtered:[],
    show:false,
    edit:false,
    dataEdit:{
      id:"",
      title:"",
      text:"",
    },
    title:"",
    text:"",
    value:0
  }
} 
    // Api for fetching todos from database
    componentDidMount() {
      axios.get(`../php/index.php`)
      .then(res => {
        const todos = res.data;
        this.setState({todos});
      }).catch(error => console.log(error));
    }

    // Toggle Complete --> click on checkbox-icon 
    markComplete = (id,completed) => {
      completed === "yes" ? completed = "no":completed = "yes";
      const completedTodo = new FormData();
      completedTodo.append("completedTodo",completed);
      completedTodo.append("todo_id",id);
     
      fetch("../php/index.php",{
        method: "POST",
        body: completedTodo
      }).then(a => console.log(a) )
      .catch(res => console.log(res))

      this.setState({
        todos: this.state.todos.map(todo => {
          if (todo.id === id) todo.completed = completed;
          console.log(id,completed,todo.id);          
          return todo;
        })
      });
    };
  
    // Delete Todo
    delTodo = (id) => {
      this.setState({
        todos: this.state.todos.filter(todo => {
          if (todo.id !== id) return todo;
        })
      });

      const delTodo = new FormData();
      delTodo.append("id",id);
      delTodo.append("deletedTodo","deletedTodo");

     
      fetch("../php/index.php",{
        method: "POST",
        body: delTodo
      }).then(a => console.log(a)).catch(error => console.log(error))
    };

    // Add Todo
    handleSubmit(e,dataEdit,edit) {
      e.preventDefault(); 

      let now = new Date();
      let month = ("0" + (now.getMonth() + 1)).slice(-2);
      let year = now.getFullYear();
      let day = ("0" + now.getDate()).slice(-2);

      // To edit Todo      
      if (edit) {
        const editedTodo = new FormData();
        editedTodo.append("todo_title",this.state.title);
        editedTodo.append("todo_text",this.state.text);
        editedTodo.append("todo_id",dataEdit.id);
        editedTodo.append("editedTodo",true);
        
        fetch("../php/index.php",{
            method: "POST",
            body: editedTodo
        }).then(a => console.log(a)).catch(res => console.log(res))

        this.setState({
          todos: this.state.todos.map(todo => {
            if (todo.id === dataEdit.id) {
              todo.title = this.state.title;   
              todo.text = this.state.text;
            }        
            return todo;
          })
        });
        this.setState({edit:false,show:false})  
      }
      // Add Todo
      else {
        this.setState({dataEdit:{title:"",text:""}});
        const newTodo = {
          id:uuid(),
          title:this.state.title,
          text:this.state.text,
          date:`${year}-${month}-${day}`,
          completed:false
        };

        this.setState({todos: [...this.state.todos, newTodo], show:false});
        
        const todo = new FormData();
        todo.append("todo_title",this.state.title);
        todo.append("todo_text",this.state.text);
        todo.append("addTodo",true);
  
        fetch("../php/index.php",{
            method: "POST",
            body: todo
        }).then(a => console.log(a)).catch(res => console.log(res))
      }
    }

    // Search todos
    search = (e) => {
      let currentTodos = [];
      let newList = [...this.state.filtered];
      let input = e.target.value.toLowerCase();

      currentTodos = this.state.todos.filter(todo => {
        if (todo.title.toLowerCase().includes(input) === true || 
            todo.text.toLowerCase().includes(input) === true) 
        return todo;      
        else newList.push(todo)
      });

      this.setState({filtered: newList.filter((todo) => {
        if (todo.title.toLowerCase().includes(input) === false ||
            todo.text.toLowerCase().includes(input) === false)
          return todo;
        else currentTodos.push(todo);
      })});
      
      this.setState({todos: currentTodos})
    }

    // Show and hide AddTodo dialog on the button plus
    toggle = (e) => {
      this.setState({title:"",text:""});
      this.setState(this.state.show === true ? {show:false}:{show:true})
    }

    // Detect a click outside AddTodo to hide it 
    hide_AddTodo = (e) => {
      if (this.state.show === true && e.target.closest("form") === null) {
        this.setState({show:false,edit:false})  
      }
    }
    
    // Show and hide AddTodo dialog on the button edit  
    editTodo = (id,title,text) => {
      this.setState({show:true, edit:true, dataEdit:{id,title,text}});       
      if (this.state.value === 0) this.setState({title,text,value:1})
      else this.setState({title,text,value:0})
    }
    
    // Input data in AddTodo
    onChange = (e) => {
      this.setState({[e.target.name]:e.target.value})      
    };

    render() {
      const {show,edit,dataEdit,text,title,todos} = this.state 
      if (show) disableScroll.on(); // prevent scrolling
      else disableScroll.off(); // re-enable scroll

      return (
        <Router>
          <Switch>
            <Route
              exact
              path="/react_todo/build/"
              render={props => (
              <React.Fragment>
                <div className="App" style={{background: show ? "#718096bb":"#edf2f7"}}
                  onClick={this.hide_AddTodo}>
                  <AddTodo title={title} text={text} onChange={this.onChange}
                  dataEdit={dataEdit} edit={edit} handleSubmit={this.handleSubmit} show={show}/>
                  <Header show={show} toggle={this.toggle} search={this.search}/>
                  <Todos show={show} editTodo={this.editTodo} todos={todos}
                  delTodo={this.delTodo} markComplete={this.markComplete}/>
                </div>  
              </React.Fragment>)}/>
            <Route exact path="/about" component={About} />
            <Route component={NotFound} />
          </Switch>
        </Router>
      )
	}
}
export default App;
