import React, { Component } from 'react'
import PropTypes from 'prop-types'

class AddTodo extends Component {
    render() {       
        const {edit,dataEdit} = this.props;
        return (
            <form id="form"  style={{display: this.props.show ? "flex":"none"}}  action="">
              <label className="">Title</label>
              <input value={this.props.title} onChange={this.props.onChange} name="title" type="text" placeholder="title"/>
              <label className="title">Description</label>
              <textarea value={this.props.text}  onChange={this.props.onChange} name="text" 
              className="description" placeholder="desription">
              </textarea>
              <button onClick={(e) => this.props.handleSubmit(e,dataEdit,edit)} className="button__add">add</button>
            </form>
        )
    }
}

AddTodo.propTypes = {
    edit: PropTypes.bool.isRequired,
    show: PropTypes.bool.isRequired,
    // addTodo: PropTypes.func.isRequired,
    onChange: PropTypes.func.isRequired,
    dataEdit: PropTypes.object.isRequired,
    title: PropTypes.string.isRequired,
    text: PropTypes.string.isRequired
}

export default AddTodo;