import React, { Component } from 'react'
import PropTypes from 'prop-types'
import Checkbox from "../components/svgIcons/Checkbox"
import DateIcon from "../components/svgIcons/DateIcon"
import Trash from "../components/svgIcons/Trash"
import EditIcon from "../components/svgIcons/EditIcon"

class TodoItem extends Component {
    render() {
        const {id,title,text,date,completed} = this.props.todo; 
        // let btnClass = ""  ;
        // if (completed === "yes") {
        //     btnClass = "";
        // } else {
        //     btnClass = "completed";
        // }

        let btnClass = completed === "yes"? "" : "completed";

        return (
            <div  className="todo__wrapper">
                <div className = "todo">
                    <div className="todo__title">
                        <p className="todo__title-text" >{title}</p>
                        <button onClick={this.props.markComplete.bind(this,id,completed)}
                        className={"todo__title-button " + btnClass}>
                            <Checkbox/>
                        </button>
                    </div>
                    <div className="todo__text">{text}</div>
                    <div className="todo__control">
                        <span>
                            <DateIcon/>
                        </span>
                        <span className="todo__control_text">{date}</span>
                        <button onClick={() => this.props.editTodo(id,title,text)} 
                        className="todo__button-edit">
                            <EditIcon/>
                        </button>
                        <button onClick={() => this.props.delTodo(id,title,text)}
                        className="todo__button-delete">
                            <Trash/>
                        </button>
                    </div>
                </div>
            </div>
        )
    }
}

// PropTypes
TodoItem.propTypes = {
    todo: PropTypes.object.isRequired,
    markComplete: PropTypes.func.isRequired,
    delTodo: PropTypes.func.isRequired,
    editTodo: PropTypes.func.isRequired,
}
export default TodoItem
