import React, { Component } from 'react'
import PropTypes from 'prop-types'
import Plus from "../components/svgIcons/Plus"
import Search from "../components/svgIcons/Search"

class Header extends Component {   
    render() {     
        return (
            <div className="header"  style={{zIndex: this.props.show ? "-190":"190"}}>
              <button onClick={(e) => this.props.toggle()} className="header__button">
                <Plus/>
              </button>
              <div class="header__logout">
                <a href="/react_todo/php/logout.php">Logout</a>
              </div>
              <div className="header__wrapper-input">
                <Search/>
                <input name="match" onChange = {this.props.search} placeholder="search" type="text"/>
              </div>
          </div>
        )
    }
}

Header.propTypes = {
  search: PropTypes.func.isRequired,
  toggle: PropTypes.func.isRequired,
}

export default Header;